const boton = document.querySelector('.boton')
const navegacion = document.querySelector('.navegacion')
const heading = document.querySelector('.heading')
const modoDark =document.querySelector('.dark-mode')
const prefiereDarkmode = window.matchMedia('(prefers-color-scheme: dark)')

// console.log(prefiereDarkmode.matches)
if(prefiereDarkmode.matches) {
    document.body.classList.add('modo-oscuro')
} else {
    document.body.classList.remove('modo-oscuro')
}
prefiereDarkmode.addEventListener('change', function(){
    if(prefiereDarkmode.matches) {
        document.body.classList.add('modo-oscuro')
    } else {
        document.body.classList.remove('modo-oscuro')
    }
})

boton.addEventListener('click', desplegar)
modoDark.addEventListener('click', function() {
    document.body.classList.toggle('modo-oscuro')
})

function desplegar(){
    boton.classList.toggle('active')
    navegacion.classList.toggle('activo')
    heading.classList.toggle('menu')
};
/* 
Para hacer funcionar el código al formmulario le he asignado con PHP las ID: id="<?php echo "form". $propiedad['id']?>" y a los botones submit la ID: id="<?php echo "submit". $propiedad['id']?>" de esta forma ambas ID son unicas y referenciadas a la id de la base de datos
*/
 
// constante hacia los botones eliminar a los que previamente se les ha añadido la clase eliminar
const botonEliminar = document.querySelectorAll('.eliminar');
 
botonEliminar.forEach(boton => {
    
    boton.addEventListener('click', function(e){
        // Prevengo ejecucion por defecto
        e.preventDefault();
        // Creo las constantes para la ID y para el mensaje
        const id = parseInt(e.target.id.substring(6));
        const mensaje = `¿Quieres eliminar la propiedad con la ID ${id}`
        
        // Condiciono el resultado de la ventana modal a la salida
        if (confirm(mensaje)) {
            document.getElementById(boton.form.attributes.id.value).submit();
        }
    });
 
});

